html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 15px;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.card {
  width: 920px;
  height: 500px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
}
.card > div {
  padding: 48px;
}
.card > div > button {
  margin-top: 75px;
}
.card > div > h1 {
  color: #f2f2f2;
  font-size: 35px;
  font-family: "Big Shoulders Display", cursive;
  font-weight: 700;
  text-transform: uppercase;
  margin: 40px 0 40px 0;
}
.card > div > p {
  color: rgba(255, 255, 255, 0.75);
  font-family: "Lexend Deca", sans-serif;
}
.card-orange {
  background-color: #e38826;
}
.card-cyan {
  background-color: #006970;
}
.card-dark-cyan {
  background-color: #004241;
}
@media (max-width: 39.9375em) {
  .card {
    width: 327px;
    height: 1326px;
    flex-direction: column;
    margin: 88px 0px 88px 0;
  }
  .card > div > button {
    margin-top: 30px;
  }
}

.bg {
  width: 100vw;
  height: 100vh;
  background-color: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 39.9375em) {
  .bg {
    height: 100%;
  }
}

.btn {
  width: 140px;
  height: 48px;
  border: none;
  border-radius: 23px;
  background-color: #f2f2f2;
  font-family: "Lexend Deca", sans-serif;
  cursor: pointer;
}
.btn-orange {
  color: #e38826;
}
.btn-orange:hover {
  color: #f2f2f2;
  background-color: #e38826;
  border: 2px solid #f2f2f2;
}
.btn-cyan {
  color: #006970;
}
.btn-cyan:hover {
  color: #f2f2f2;
  background-color: #006970;
  border: 2px solid #f2f2f2;
}
.btn-dark-cyan {
  color: #004241;
}
.btn-dark-cyan:hover {
  color: #f2f2f2;
  background-color: #004241;
  border: 2px solid #f2f2f2;
}